import React from 'react';

const Card = (props) =>{
	const {name,email,id}=props.robots;
	return (

		<div className=' bg-light-red dib br3 pa3 ma2 grow bw-2 shadow-5'>

			<img alt='robots' src={`https://robohash.org/${id}?200x200`}/>
			<div className='tc'>
				<h2> {name} </h2>
				<p> {email}</p>
				
			</div>


		</div>




		);
}

export default Card;